import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Form, Input, Button, Toast } from 'antd-mobile';
import { UserOutline, LockOutline } from 'antd-mobile-icons';
import './style.css';

const Login = ({ history }) => {
  const [loading, setLoading] = useState(false);

  const onFinish = (values) => {
    setLoading(true);
    // 
    setTimeout(() => {
      setLoading(false);
      // 
      localStorage.setItem('merchantToken', 'demo-token');
      localStorage.setItem('merchantInfo', JSON.stringify({
        id: '1',
        name: '',
        phone: '13800138000'
      }));
      Toast.show({
        icon: 'success',
        content: '',
      });
      history.push('/merchant/dashboard');
    }, 1500);
  };

  return (
    <div className="login-container">
      <div className="login-header">
        <div className="login-logo">
          <img src="/ARLlogo.svg" alt="ARL Logo" />
        </div>
        <h2>ARL</h2>
        <p></p>
      </div>

      <Form
        layout="horizontal"
        onFinish={onFinish}
        footer={
          <Button block type="submit" color="primary" loading={loading} size="large">
            
          </Button>
        }
      >
        <Form.Item
          name="username"
          rules={[{ required: true, message: '' }]}
        >
          <Input placeholder="" prefix={<UserOutline />} />
        </Form.Item>

        <Form.Item
          name="password"
          rules={[{ required: true, message: '' }]}
        >
          <Input
            placeholder=""
            type="password"
            prefix={<LockOutline />}
          />
        </Form.Item>
      </Form>

      <div className="login-footer">
        <p>
           <Link to="/register"></Link>
        </p>
      </div>
    </div>
  );
};

export default Login;