import React, { useState, useRef } from 'react';
import { useHistory } from 'react-router-dom';
import { Tabs, Card, List, Button, Dialog, Form, Input, TextArea, ImageUploader, Toast, Selector, Image, ImageViewer } from 'antd-mobile';
import { RightOutline, PictureOutline } from 'antd-mobile-icons';
import './style.css';
import '../../styles/common.css';

const FinanceCenter = () => {
  const history = useHistory();
  const [activeTab, setActiveTab] = useState('balance');
  const [rechargeModalVisible, setRechargeModalVisible] = useState(false);
  const [rechargeDetailVisible, setRechargeDetailVisible] = useState(false);
  const [consumptionDetailVisible, setConsumptionDetailVisible] = useState(false);
  const [selectedRecharge, setSelectedRecharge] = useState(null);
  const [selectedConsumption, setSelectedConsumption] = useState(null);
  const [fileList, setFileList] = useState([]);
  const formRef = useRef(null);

  // 
  const accountBalance = {
    balance: 5280.00,
    frozen: 0.00,
    available: 5280.00,
  };

  // 
  const rechargeRecords = [
    {
      id: 'R001',
      amount: 2000.00,
      method: '',
      status: 'completed',
      time: '2025-05-10 14:30',
      voucher: 'https://example.com/voucher1.jpg',
      remarks: '',
      reviewResult: '',
    },
    {
      id: 'R002',
      amount: 3000.00,
      method: '',
      status: 'completed',
      time: '2025-04-25 10:15',
      voucher: 'https://example.com/voucher2.jpg',
      remarks: '',
      reviewResult: '',
    },
    {
      id: 'R003',
      amount: 1000.00,
      method: '',
      status: 'pending',
      time: '2025-05-15 09:45',
      voucher: 'https://example.com/voucher3.jpg',
      remarks: '',
      reviewResult: '',
    },
  ];

  // 
  const consumptionRecords = [
    {
      id: 'C001',
      amount: 320.00,
      type: '',
      orderId: 'DZ10001',
      time: '2025-05-12 15:20',
      transactionId: 'TRX20250512001',
      arlTrackingNumber: 'ARL8765432',
      darazOrderId: 'DZ10001',
      feeName: '',
    },
    {
      id: 'C002',
      amount: 180.00,
      type: '',
      orderId: 'EX20001',
      time: '2025-05-11 11:30',
      transactionId: 'TRX20250511002',
      arlTrackingNumber: 'ARL8765433',
      darazOrderId: 'EX20001',
      feeName: '',
    },
    {
      id: 'C003',
      amount: 220.00,
      type: '',
      orderId: 'DZ10002',
      time: '2025-05-08 16:45',
      transactionId: 'TRX20250508003',
      arlTrackingNumber: 'ARL8765434',
      darazOrderId: 'DZ10002',
      feeName: '',
    },
  ];

  const getStatusTag = (status) => {
    let color = '';
    let text = '';

    switch (status) {
      case 'completed':
        color = '#00b578';
        text = '';
        break;
      case 'pending':
        color = '#ff8f1f';
        text = '';
        break;
      case 'failed':
        color = '#ff3141';
        text = '';
        break;
      default:
        color = '#999';
        text = '';
    }

    return <span className={`status-tag status-${status}`}>{text}</span>;
  };

  const handleRecharge = (values) => {
    console.log(':', { ...values, vouchers: fileList });
    setRechargeModalVisible(false);
    Toast.show({
      icon: 'success',
      content: '',
    });
    // 
  };

  // 
  const mockUpload = async (file) => {
    return {
      url: URL.createObjectURL(file),
    };
  };

  const goBack = () => {
    window.history.back();
  };

  return (
    <div className="finance-container">
      <div className="finance-header page-header">
        <div className="back-icon" onClick={goBack}>
          <svg width="20" height="20" viewBox="0 0 44 44" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M27.4166 14.6673L17.4166 22.0007L27.4166 29.334" stroke="currentColor" strokeWidth="3.66667" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
        </div>
        <h2></h2>
        <div className="placeholder"></div>
      </div>

      <Tabs activeKey={activeTab} onChange={setActiveTab}>
        <Tabs.Tab title="" key="balance">
          <Card className="balance-card">
            <div className="balance-info">
              <div className="balance-title"> ()</div>
              <div className="balance-amount">{accountBalance.balance.toFixed(2)}</div>
              <div className="balance-details">
                <div className="balance-detail-item">
                  <span>:</span>
                  <span>{accountBalance.available.toFixed(2)}</span>
                </div>
                <div className="balance-detail-item">
                  <span>:</span>
                  <span 
                    className="clickable-amount"
                    onClick={() => history.push('/merchant/frozen-details')}
                  >
                    {accountBalance.frozen.toFixed(2)}
                  </span>
                </div>
              </div>
              <Button
                color="primary"
                block
                onClick={() => setRechargeModalVisible(true)}
              >
                
              </Button>
            </div>
          </Card>

          <div className="record-section">
            <div className="record-header">
              <div className="record-title"></div>
              <div className="view-all" onClick={() => history.push('/merchant/recharge-records')}></div>
            </div>
            <List className="finance-list">
              {rechargeRecords.slice(0, 5).map((record) => (
                <List.Item
                  key={record.id}
                  extra={<RightOutline />}
                  onClick={() => {
                    setSelectedRecharge(record);
                    setRechargeDetailVisible(true);
                  }}
                  arrow={false}
                >
                  <div className="finance-record">
                    <div className="finance-record-header">
                      <div className="finance-record-amount">+{record.amount.toFixed(2)}</div>
                      {getStatusTag(record.status)}
                    </div>
                    <div className="finance-record-info">
                      <div>: {record.method}</div>
                      <div>: {record.time}</div>
                    </div>
                  </div>
                </List.Item>
              ))}
            </List>
          </div>

          <div className="record-section">
            <div className="record-header">
              <div className="record-title"></div>
              <div className="view-all" onClick={() => history.push('/merchant/consumption-records')}></div>
            </div>
            <List className="finance-list">
              {consumptionRecords.slice(0, 5).map((record) => (
                <List.Item
                  key={record.id}
                  extra={<RightOutline />}
                  onClick={() => {
                    setSelectedConsumption(record);
                    setConsumptionDetailVisible(true);
                  }}
                  arrow={false}
                >
                  <div className="finance-record">
                    <div className="finance-record-header">
                      <div className="finance-record-amount consumption">-{record.amount.toFixed(2)}</div>
                    </div>
                    <div className="finance-record-info">
                      <div>: {record.type}</div>
                      <div>: {record.orderId}</div>
                      <div>: {record.time}</div>
                    </div>
                  </div>
                </List.Item>
              ))}
            </List>
          </div>
        </Tabs.Tab>

        <Tabs.Tab title="" key="recharge">
          <Card className="recharge-card">
            <div className="recharge-info">
              <div className="recharge-title"></div>
              <div className="recharge-subtitle"></div>

              <Form
                layout="horizontal"
                onFinish={handleRecharge}
                footer={
                  <Button block type="submit" color="primary" size="large">
                    
                  </Button>
                }
              >
                <Form.Item
                  name="amount"
                  label=""
                  rules={[{ required: true, message: '' }]}
                >
                  <Input type="number" placeholder="" />
                </Form.Item>

                <Form.Item
                  name="method"
                  label=""
                  rules={[{ required: true, message: '' }]}
                >
                  <Selector
                    options={[
                      { label: '', value: 'alipay' },
                      { label: '', value: 'bank' },
                    ]}
                  />
                </Form.Item>



                <Form.Item
                  name="voucher"
                  label=""
                  rules={[{ required: true, message: '' }]}
                >
                  <ImageUploader
                    value={fileList}
                    onChange={setFileList}
                    upload={mockUpload}
                    multiple={false}
                    maxCount={1}
                    showUpload={fileList.length < 1}
                    beforeUpload={(file) => {
                      if (file.size > 10 * 1024 * 1024) {
                        Toast.show('10MB');
                        return null;
                      }
                      return file;
                    }}
                  >
                    <div className="upload-button">
                      <PictureOutline fontSize={24} />
                      <span></span>
                    </div>
                  </ImageUploader>
                </Form.Item>

                <Form.Item
                  name="remarks"
                  label=""
                >
                  <TextArea placeholder="" maxLength={100} rows={2} />
                </Form.Item>
              </Form>
            </div>
          </Card>
        </Tabs.Tab>
      </Tabs>

      <Dialog
        visible={rechargeModalVisible}
        title=""
        content={
          <Form layout="horizontal" onFinish={handleRecharge} ref={formRef}>
            <Form.Item
              name="amount"
              label=""
              rules={[{ required: true, message: '' }]}
            >
              <Input type="number" placeholder="" />
            </Form.Item>

            <Form.Item
              name="method"
              label=""
              rules={[{ required: true, message: '' }]}
            >
              <Selector
                options={[
                  { label: '', value: 'alipay' },
                  { label: '', value: 'bank' },
                ]}
              />
            </Form.Item>



            <Form.Item
              name="voucher"
              label=""
              rules={[{ required: true, message: '' }]}
            >
              <ImageUploader
                value={fileList}
                onChange={setFileList}
                upload={mockUpload}
                multiple={false}
                maxCount={1}
                showUpload={fileList.length < 1}
                beforeUpload={(file) => {
                  if (file.size > 10 * 1024 * 1024) {
                    Toast.show('10MB');
                    return null;
                  }
                  return file;
                }}
              >
                <div className="upload-button">
                  <PictureOutline fontSize={24} />
                  <span></span>
                </div>
              </ImageUploader>
            </Form.Item>

            <Form.Item
              name="remarks"
              label=""
            >
              <TextArea placeholder="" maxLength={100} rows={2} />
            </Form.Item>
          </Form>
        }
        closeOnAction
        onClose={() => setRechargeModalVisible(false)}
        actions={[
          {
            key: 'cancel',
            text: '',
          },
          {
            key: 'confirm',
            text: '',
            bold: true,
            danger: false,
            onClick: () => {
              // FormonFinish
              if (formRef.current) {
                formRef.current.submit();
              }
            },
          },
        ]}
      />

      {/*  */}
      <Dialog
        visible={rechargeDetailVisible}
        title=""
        content={
          selectedRecharge && (
            <div className="recharge-detail">
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedRecharge.amount.toFixed(2)} </span>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedRecharge.method}</span>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <div className="detail-value">
                  <img 
                    src={selectedRecharge.voucher} 
                    alt="" 
                    style={{ width: '100%', maxHeight: '200px', objectFit: 'contain' }} 
                    onClick={() => {
                      ImageViewer.Multi.show({ images: [selectedRecharge.voucher] });
                    }}
                  />
                </div>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedRecharge.remarks || ''}</span>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedRecharge.time}</span>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedRecharge.reviewResult}</span>
              </div>
            </div>
          )
        }
        closeOnAction
        onClose={() => setRechargeDetailVisible(false)}
        actions={[
          {
            key: 'close',
            text: '',
          },
        ]}
      />

      {/*  */}
      <Dialog
        visible={rechargeDetailVisible}
        title=""
        content={
          selectedRecharge && (
            <div className="recharge-detail">
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedRecharge.amount.toFixed(2)} </span>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedRecharge.method}</span>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <div className="detail-value">
                  <img 
                    src={selectedRecharge.voucher} 
                    alt="" 
                    style={{ width: '100%', maxHeight: '200px', objectFit: 'contain' }} 
                    onClick={() => {
                      ImageViewer.Multi.show({ images: [selectedRecharge.voucher] });
                    }}
                  />
                </div>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedRecharge.remarks || ''}</span>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedRecharge.time}</span>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedRecharge.reviewResult}</span>
              </div>
            </div>
          )
        }
        closeOnAction
        onClose={() => setRechargeDetailVisible(false)}
        actions={[
          {
            key: 'close',
            text: '',
          },
        ]}
      />

      {/*  */}
      <Dialog
        visible={consumptionDetailVisible}
        title=""
        content={
          selectedConsumption && (
            <div className="recharge-detail">
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedConsumption.transactionId}</span>
              </div>
              <div className="detail-item">
                <span className="detail-label">ARL:</span>
                <span className="detail-value">{selectedConsumption.arlTrackingNumber}</span>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedConsumption.darazOrderId}</span>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedConsumption.feeName}</span>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value consumption">{selectedConsumption.amount.toFixed(2)} </span>
              </div>
              <div className="detail-item">
                <span className="detail-label">:</span>
                <span className="detail-value">{selectedConsumption.time}</span>
              </div>
            </div>
          )
        }
        closeOnAction
        onClose={() => setConsumptionDetailVisible(false)}
        actions={[
          {
            key: 'close',
            text: '',
          },
        ]}
      />
    </div>
  );
};

export default FinanceCenter;