/**
 * SLA
 */

/**
 * SLA
 * @param {string} status - 
 * @param {string} orderTime - YYYY-MM-DD HH:MM
 * @returns {number} - SLA
 */
export const calculateRemainingSlA = (status, orderTime) => {
  // SLA
  const SLA_HOURS = {
    // 
    merchant_pending_shipment: 24,    // 24
    merchant_shipped: 48,            // 48
    domestic_warehouse_processing: 72, // 72
    main_line_transport: 120,        // 120
    overseas_warehouse_processing: 48, // 48
    overseas_warehouse_shipped: 0,   // 0
    
    // 
    pending: 24,                     // 24
    packed: 12,                      // 12
    ready_to_ship: 24,               // 24
    shipped: 72,                     // 72
    delivered: 0,                    // 0
    failed_delivery: 48,             // 48
  };
  
  // SLA
  const totalSlaHours = SLA_HOURS[status] || 0;
  if (totalSlaHours === 0) return 0;
  
  // 
  const orderDate = new Date(orderTime.replace(/-/g, '/'));
  const currentDate = new Date();
  
  // 
  const elapsedHours = Math.floor((currentDate - orderDate) / (1000 * 60 * 60));
  
  // 
  const remainingHours = Math.max(0, totalSlaHours - elapsedHours);
  
  return remainingHours;
};

/**
 * SLA
 * @param {number} hours - 
 * @returns {string} - 
 */
export const formatRemainingSlA = (hours) => {
  if (hours <= 0) return '';
  
  const days = Math.floor(hours / 24);
  const remainingHours = hours % 24;
  
  if (days > 0) {
    return `${days}${remainingHours}`;
  }
  
  return `${remainingHours}`;
};