import React, { useState } from 'react';
import { NavBar, Form, DatePicker, Button, Empty, List, Tag } from 'antd-mobile';
import { SearchOutline, CloseOutline } from 'antd-mobile-icons';
import './style.css';
import '../../styles/common.css';

const ReturnQuery = () => {
  const [returnList, setReturnList] = useState([]);
  const [searchVisible, setSearchVisible] = useState(false);
  const [form] = Form.useForm();

  // 
  const handleSearch = (values) => {
    console.log(':', values);
    // API
    const mockData = [
      {
        id: 'RT2023112001',
        orderNo: 'OR2023101501',
        returnDate: '2023-11-20',
        status: '',
        reason: '',
        items: [
          { name: 'A', quantity: 1, price: '99.00' }
        ]
      },
      {
        id: 'RT2023111501',
        orderNo: 'OR2023100801',
        returnDate: '2023-11-15',
        status: '',
        reason: '',
        items: [
          { name: 'B', quantity: 2, price: '199.00' }
        ]
      },
      {
        id: 'RT2023110501',
        orderNo: 'OR2023092201',
        returnDate: '2023-11-05',
        status: '',
        reason: '',
        items: [
          { name: 'C', quantity: 1, price: '299.00' }
        ]
      }
    ];
    
    setReturnList(mockData);
    setSearchVisible(false);
  };

  const toggleSearch = () => {
    setSearchVisible(!searchVisible);
  };

  const resetForm = () => {
    form.resetFields();
  };

  const getStatusTag = (status) => {
    switch (status) {
      case '':
        return <Tag color="#faad14"></Tag>;
      case '':
        return <Tag color="#52c41a"></Tag>;
      case '':
        return <Tag color="#ff4d4f"></Tag>;
      default:
        return <Tag color="#1677ff">{status}</Tag>;
    }
  };

  return (
    <div className="return-query-container">
      <NavBar back={null} className="styled-header"></NavBar>
      
      <div className="search-bar">
        <div className="search-button" onClick={toggleSearch}>
          <SearchOutline /> 
        </div>
      </div>

      {searchVisible && (
        <div className="search-form">
          <Form
            form={form}
            layout="horizontal"
            footer={
              <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                <Button onClick={resetForm}></Button>
                <Button type="submit" color="primary" onClick={() => form.submit()}></Button>
              </div>
            }
            onFinish={handleSearch}
          >
            <Form.Header></Form.Header>
            <Form.Item name="returnId" label="">
              <input placeholder="" />
            </Form.Item>
            <Form.Item name="orderNo" label="">
              <input placeholder="" />
            </Form.Item>
            <Form.Item name="startDate" label="">
              <DatePicker>
                {value => value ? value.toLocaleDateString() : ''}
              </DatePicker>
            </Form.Item>
            <Form.Item name="endDate" label="">
              <DatePicker>
                {value => value ? value.toLocaleDateString() : ''}
              </DatePicker>
            </Form.Item>
          </Form>
          <div className="close-search" onClick={toggleSearch}>
            <CloseOutline />
          </div>
        </div>
      )}

      <div className="return-list">
        {returnList.length > 0 ? (
          returnList.map(item => (
            <div key={item.id} className="return-card">
              <div className="return-header">
                <div className="return-id">{item.id}</div>
                <div>{getStatusTag(item.status)}</div>
              </div>
              <div className="return-info">
                <div>{item.orderNo}</div>
                <div>{item.returnDate}</div>
                <div>{item.reason}</div>
              </div>
              <List header="">
                {item.items.map((product, index) => (
                  <List.Item key={index} extra={product.price}>
                    <div>{product.name}</div>
                    <div className="product-quantity">x{product.quantity}</div>
                  </List.Item>
                ))}
              </List>
            </div>
          ))
        ) : (
          <Empty
            description=""
            style={{ padding: '64px 0' }}
          />
        )}
      </div>
    </div>
  );
};

export default ReturnQuery;