// 
export const ORDER_STATUS = {
  // 
  MERCHANT_PENDING_SHIPMENT: 'merchant_pending_shipment',  // 
  MERCHANT_SHIPPED: 'merchant_shipped',  // 
  DOMESTIC_WAREHOUSE_PROCESSING: 'domestic_warehouse_processing',  // 
  MAIN_LINE_TRANSPORT: 'main_line_transport',  // 
  OVERSEAS_WAREHOUSE_PROCESSING: 'overseas_warehouse_processing',  // 
  OVERSEAS_WAREHOUSE_SHIPPED: 'overseas_warehouse_shipped',  // 
  
  // 
  PENDING: 'pending',                // 
  PACKED: 'packed',                  // 
  READY_TO_SHIP: 'ready_to_ship',    // 
  SHIPPED: 'shipped',                // 
  DELIVERED: 'delivered',            // 
  FAILED_DELIVERY: 'failed_delivery', // 
  CANCELED: 'canceled'               // 
};

// 
export const ORDER_STATUS_TEXT = {
  // 
  [ORDER_STATUS.MERCHANT_PENDING_SHIPMENT]: '',
  [ORDER_STATUS.MERCHANT_SHIPPED]: '',
  [ORDER_STATUS.DOMESTIC_WAREHOUSE_PROCESSING]: '',
  [ORDER_STATUS.MAIN_LINE_TRANSPORT]: '',
  [ORDER_STATUS.OVERSEAS_WAREHOUSE_PROCESSING]: '',
  [ORDER_STATUS.OVERSEAS_WAREHOUSE_SHIPPED]: '',
  
  // 
  [ORDER_STATUS.PENDING]: '',
  [ORDER_STATUS.PACKED]: '',
  [ORDER_STATUS.READY_TO_SHIP]: '',
  [ORDER_STATUS.SHIPPED]: 'shipped',
  [ORDER_STATUS.DELIVERED]: '',
  [ORDER_STATUS.FAILED_DELIVERY]: '',
  [ORDER_STATUS.CANCELED]: ''
};

// 
export const ORDER_STATUS_COLOR = {
  // 
  [ORDER_STATUS.MERCHANT_PENDING_SHIPMENT]: '#ff8f1f',  // 
  [ORDER_STATUS.MERCHANT_SHIPPED]: '#faad14',  // 
  [ORDER_STATUS.DOMESTIC_WAREHOUSE_PROCESSING]: '#1677ff',  // 
  [ORDER_STATUS.MAIN_LINE_TRANSPORT]: '#1677ff',  // 
  [ORDER_STATUS.OVERSEAS_WAREHOUSE_PROCESSING]: '#1677ff',  // 
  [ORDER_STATUS.OVERSEAS_WAREHOUSE_SHIPPED]: '#00b578',  // 
  
  // 
  [ORDER_STATUS.PENDING]: '#ff8f1f',       // 
  [ORDER_STATUS.PACKED]: '#faad14',        // 
  [ORDER_STATUS.READY_TO_SHIP]: '#ff8f1f', // 
  [ORDER_STATUS.SHIPPED]: '#1677ff',       // 
  [ORDER_STATUS.DELIVERED]: '#00b578',     // 
  [ORDER_STATUS.FAILED_DELIVERY]: '#ff4d4f', // 
  [ORDER_STATUS.CANCELED]: '#cccccc'       // 
};

/**
 * 
 * @param {string} status - 
 * @returns {object} - 
 */
export const getOrderStatusInfo = (status) => {
  return {
    text: ORDER_STATUS_TEXT[status] || '',
    color: ORDER_STATUS_COLOR[status] || '#999',
  };
};

/**
 * 
 * @returns {Array} - 
 */
export const getOrderStatusSteps = () => [
  {
    title: ORDER_STATUS_TEXT[ORDER_STATUS.MERCHANT_PENDING_SHIPMENT],
    description: '',
  },
  {
    title: ORDER_STATUS_TEXT[ORDER_STATUS.MERCHANT_SHIPPED],
    description: '',
  },
  {
    title: ORDER_STATUS_TEXT[ORDER_STATUS.DOMESTIC_WAREHOUSE_PROCESSING],
    description: '',
  },
  {
    title: ORDER_STATUS_TEXT[ORDER_STATUS.MAIN_LINE_TRANSPORT],
    description: '',
  },
  {
    title: ORDER_STATUS_TEXT[ORDER_STATUS.OVERSEAS_WAREHOUSE_PROCESSING],
    description: '',
  },
  {
    title: ORDER_STATUS_TEXT[ORDER_STATUS.OVERSEAS_WAREHOUSE_SHIPPED],
    description: '',
  },
];