import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Form, Input, Button, Toast } from 'antd-mobile';
import { UserOutline, LockOutline, PhoneFill, MailOutline } from 'antd-mobile-icons';
import './style.css';

const Register = ({ history }) => {
  const [loading, setLoading] = useState(false);

  const onFinish = (values) => {
    setLoading(true);
    // 
    setTimeout(() => {
      setLoading(false);
      Toast.show({
        icon: 'success',
        content: '',
      });
      history.push('/login');
    }, 1500);
  };

  return (
    <div className="register-container">
      <div className="register-header">
        <div className="register-logo">
          <img src="/ARLlogo.svg" alt="ARL Logo" />
        </div>
        <h2>ARL</h2>
        <p></p>
      </div>

      <Form
        layout="horizontal"
        onFinish={onFinish}
        footer={
          <Button block type="submit" color="primary" loading={loading} size="large">
            
          </Button>
        }
      >
        <Form.Item
          name="username"
          rules={[{ required: true, message: '' }]}
        >
          <Input placeholder="" prefix={<UserOutline />} />
        </Form.Item>

        <Form.Item
          name="phone"
          rules={[{ required: true, message: '' }]}
        >
          <Input placeholder="" prefix={<PhoneFill />} />
        </Form.Item>

        <Form.Item
          name="email"
          rules={[{ required: true, message: '' }]}
        >
          <Input placeholder="" prefix={<MailOutline />} />
        </Form.Item>

        <Form.Item
          name="password"
          rules={[{ required: true, message: '' }]}
        >
          <Input
            placeholder=""
            type="password"
            prefix={<LockOutline />}
          />
        </Form.Item>

        <Form.Item
          name="confirmPassword"
          rules={[
            { required: true, message: '' },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue('password') === value) {
                  return Promise.resolve();
                }
                return Promise.reject(new Error(''));
              },
            }),
          ]}
        >
          <Input
            placeholder=""
            type="password"
            prefix={<LockOutline />}
          />
        </Form.Item>
      </Form>

      <div className="register-footer">
        <p>
           <Link to="/login"></Link>
        </p>
      </div>
    </div>
  );
};

export default Register;